
#navbar {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 99;
    background-color: #1f2833;
    padding: 1%;
    opacity: 0.9;

}

#navbar a {
    padding: 1%;
    margin: 0;
    color: white;
    text-decoration: none
}

#navbar a:hover {
    background-color: #ddd;
    color: #9180e7;
    padding: 1%;
}


#navbar a.active {
    background-color: #4CAF50;
    color: white;
    width: 100%;

}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 12px;
    background-color: #4CAF50;
}
